body {
	background-color: #191f2c;
	color: #fff;
	font-family: 'NanumSquareRound', sans-serif;
}

.Dice {
	width: 100%;
	max-width: 100px;
	max-height: 100px;
}

.Board {
	display: inline-block;
	padding: 35px 30px;
	border: 1px solid transparent;
	border-radius: 12px;
	font-size: 16px;
	text-align: center;
	background-color: #272b38;
}

.Board.Board-winner {
	border: 1px solid #fff;
	box-shadow: 0 0 12px 0 hsla(0, 0%, 100%, 0.48);
}

.Board .Board-heading {
	margin: 45px auto 15px;
	font-size: 18px;
	font-weight: 400;
	color: #888d9d;
}

.Board .Board-heading:first-child {
	margin-top: 0;
}

.App {
	width: 100%;
	max-width: 640px;
	margin: 20px auto;
	text-align: center;
}

.App .App-logo {
	height: 65px;
}

.App .App-title {
	margin: 10px auto 28px;
	font-size: 28px;
}

.App .App-button {
	margin: 6px;
}

.App .App-boards {
	margin-top: 27px;
}

.App .App-board {
	width: 30%;
	margin: 10px;
}
